export async function storeUserVal(idStr: string, val: any)
{
  const db2 = await openDBlocal('carin', 3);
  await db2.put('user', { id: idStr, value: val });
  db2.close();
}

export async function storeVallocal(idStr: string, val: number) {

  var i: Number = 1;

  const db2 = await openDBlocal('carin', 3);
  db2.put('settings', { id: idStr, value: val });
  db2.close();

  return i;
}

export async function addVideoCode(code: string) {
  const db = await openDBlocal('carin', 3);
  db.put('videos', { id: code, value: 1 }); // TODO: make array ??, because we might need to port it back to the simpler localstorage
}

export async function getleaks():Promise<EventLSG[]> {
  const db = await openDBlocal('carin', 3);
  const events = await db.getAll('events');
 
  db.close();
   // loop over events and filter leak events 
   const leaks: EventLSG[] = events.filter(event => event.value.type === eTypes.LEAK);
  return leaks;
}




 async function addEventLocal(event: EventLSG) {
  const db = await openDBlocal('carin', 3);
  var counter:number = store_eventsInBackLogToBeSendToMongoDBValue;

  db.put('events', { id: counter, value: event }); 
  counter++;
  store_eventsInBackLogToBeSendToMongoDB.set(counter);
  db.close();

}


export async function storeRemoteUserAndLog(logs: EventLSG[], user: UserData) {
  user.user_code = getUniqueUUIDandCreateIfNeeded();
  storeVallocal("user_id", user.user_code);
  RealmStoreUserAndLog(logs, user);

}

export async function createAndStoreLeak(dateAndTime: Date, activity: number) {
  var extraInfo: string = "Activity: " + activity + " - Day: " + store_weeknrValue;
  var event: EventLSG = { type: eTypes.LEAK, value: 0, date: dateAndTime, extra_info: extraInfo };
  var user:UserData;


  
  var RetVal = await RealmStoreUserAndLog([event], user );
//  if (RetVal == 0) {
    // error store event locally
    addEventLocal(event)
    console.log(RetVal);
 // }

}

export async function convertToLeak(json: JSON[]):Promise<boolean> {
  // for element in array
  var booleanRetVal: boolean = false;

  var activity: number = 0;
  var d = new Date(0); // The 0 there is the key, which sets the date to the epoch


  var valid = 0;
  for (var i = 0; i < json.length; i++) {
    var element: JSON = json[i];
    if (element.hasOwnProperty("EVENT")) {
      if (element["EVENT"] == "LEAK") {
        valid += 1;
      }

    }

    if (element.hasOwnProperty("TMR")) {
      valid += 1;

      // get key value from json and convert to date
      var utcSeconds = element["TMR"];
      d.setUTCSeconds(utcSeconds);
    }

    if (element.hasOwnProperty("ACT")) {
      valid += 1;
      activity = element["ACT"];
    
    }
    // ignore quantity for now

  }

  if (valid == 3) {
    createAndStoreLeak(d, activity);
    booleanRetVal = true;
  }

  return booleanRetVal;
}

    
    



