/**
 * Import LitElement base class, html helper function,
 * and TypeScript decorators
 **/
import {
  LitElement, html, customElement, property
} from 'lit-element';
/*import {classMap} from 'lit-html/directives/class-map.js'; */
import BTLEclass from './BTLEclass'

export enum BTLE_Action {
  'read', 'write','enableNotify','disableNotify'
}

export interface BTLE_Struct {
  serviceNr: number,
  action: BTLE_Action, 
  data: DataView
}

/**
 * Use the customElement decorator to define your class as
 * a custom element. Registers <my-element> as an HTML tag.
 */
@customElement('lsg-bluetooth')
export class LSG_bluetooth extends LitElement{

  
  /** 
   * Create an observed property. Triggers update on change. 
   */

  @property() name: string = 'nameit';
  @property() connectedStr: String = 'connect';
  @property() connected: boolean = false;
  @property({type : Array})  primaryServiceObject = [[{service: '00001523-3edb-ecac-9b4b-631f8a7f885e', characteristic: '00001525-3edb-ecac-9b4b-631f8a7f885e'}]] ;
  @property() read: boolean = false;
  @property() data: DataView[];
  @property() btledata: BTLE_Struct[] = null;
  @property() backgroundImage: string = 'url("./sensorIcon.svg")';
  
  constructor() {
    super();
    this._handleBtGattStateChange = this._handleBtGattStateChange.bind(this);
    this._handleNotifyChanged = this._handleNotifyChanged.bind(this);
 
  }

  // if you want all css styles from the html page enable this:
  // createRenderRoot() {
  //   return this;
  // }

  buttonColour = "var(--lsg-blue)";
  buttonPointer = 'pointer';
  bluetoothDevice:any = null;
  bluetoothGatt:BTLEclass = null;
  prevConnectedStr: String = '';


  bluetoothCallback(constr:string)
  {
     this.connectedStr = constr;
  }
  /**
   * Implement `render` to define a template for your element.
   */
  render() {
    /**
     * Use JavaScript expressions to include property values in
     * the element template.
     */
    
    if (this.connectedStr == 'stop')
    {
      this.connect();
    }

    if (this.connectedStr == "connected")
    {
       this.buttonColour = "var(--lsg-blue)";
     //  document.getElementById("buttoninfo").style.display = "none";
       
       if (this.btledata != null)
       {
         this.processBTLEcommands();
         //for (const btleAction of this.btledata){
         //  console.log(btleAction);
          //}
          this.btledata = null;
       }
       if (this.data != null) // when the user wants to write data
       {
          var sendd = this.data; // we make this.data null so use a var for the second write
          this.bluetoothGatt.writevalueToService(sendd[0],0)
          .then(value => {
            if (sendd.length > 1)
            {
              this.bluetoothGatt.writevalueToService(sendd[1],0);
            }
          });
          this.data = null;
       }
       if (this.read) // when the user wants to read for a charactistic 
       {
          this.readBtle(0);
       }
      
    }else
    if (this.connectedStr == "connect")
    {
       this.buttonColour = 'var(--lsg-blue)' ;
     //  document.getElementById("buttoninfo").style.display = "none";
    }else 
    if (this.connectedStr == "connecting")
    {
      this.buttonColour = 'var(--lsg-blue-semilight)';
    //  document.getElementById("buttoninfo").style.display = "block";
    }else 
    if (this.connectedStr == "disconnecting")
    {
      this.buttonColour = 'var(--lsg-blue-semilight)';
    //  document.getElementById("buttoninfo").style.display = "none";
    }
    else
    {
      this.buttonColour = 'var(--warning-color)';;
    }

    this.prevConnectedStr = this.connectedStr;

    return html`
       <style>
        .button {
        background-color: ${this.buttonColour}; 
        background-image: ${this.backgroundImage};
        background-repeat: no-repeat;
        width: 80%;
        margin-left: 10%;
        color: var(--btn-color, blue);
        padding: 10px;
        text-align: center;
        display: inline-block;
         border: none;

        cursor: ${this.buttonPointer};
        }
      </style>
      <button class="button" @click="${this.connect}"> ${this.name}</button>`;
  }

  async connect() {
    let oldstate = this.connectedStr;

    if (this.bluetoothGatt != null)
    {
        this.bluetoothGatt.disconnect(this.bluetoothGatt);  // disconnect old gatt
    }
    if (oldstate == "connect")
    {
      try {
        var servicelist = [this.primaryServiceObject[0][0].service];
        if (typeof (this.primaryServiceObject[1]) !== 'undefined') {
          servicelist.push(this.primaryServiceObject[1][0].service);
        }
          
        this.bluetoothDevice = await navigator.bluetooth.requestDevice({
          filters: [{ services: servicelist  }],
          optionalServices: servicelist

          //filters: [{ services: [this.streamingService, this.controlService] }],
          //optionalServices: [this.controlService, this.streamingService]
        });

        console.log(this.bluetoothDevice.name)

        this.bluetoothGatt = new BTLEclass(this.bluetoothDevice, this.primaryServiceObject);
        // need to bind to the class else we cannot call anything inside the bluetooth class
        // https://stackoverflow.com/questions/43727516/javascript-how-adding-event-handler-inside-a-class-with-a-class-method-as-the-c/43727582
        this.bluetoothDevice.addEventListener('gattserverdisconnected', this.bluetoothGatt.onDisconnected.bind(this.bluetoothGatt));
        this.bluetoothGatt.addEventListener('state-change', this._handleBtGattStateChange);
        this.bluetoothGatt.addEventListener('notify-data-event', this._handleNotifyChanged);
        this.bluetoothGatt.connect();
        
       
      } catch (error) {
        console.log('Argh! ' + error);
      }
    }
  }
   
  async readBtle(serviceNr:number)
  {
    await this.bluetoothGatt.readvalueFromService(serviceNr)
      .then(value => {
        this.read = false;
        let myEvent = new CustomEvent('data-event', {
          detail: { message: value },
          bubbles: true,
          composed: true
        });
        this.dispatchEvent(myEvent);
      }, reason => {
        this.read = false;
      });
  }
  async processBTLEcommands()
  { 
    for (const btleAction of this.btledata){
    
      if (btleAction.action == BTLE_Action.write)
        await this.bluetoothGatt.writevalueToService(btleAction.data, btleAction.serviceNr);
      if  (btleAction.action == BTLE_Action.read)
        await this.readBtle(btleAction.serviceNr); 
      if (btleAction.action == BTLE_Action.enableNotify)
        await this.bluetoothGatt.SetNotify(true, btleAction.serviceNr);
      if (btleAction.action == BTLE_Action.disableNotify)
        await this.bluetoothGatt.SetNotify(false, btleAction.serviceNr);
    }
  }

  _handleBtGattStateChange(evt:any) {
    // update this.connectedStr when the BTLE's stage changes

    this.connectedStr = evt.target.connectionStr;
    if (this.connectedStr == 'connected')
    {
      let myEvent = new CustomEvent('data-event', {
        detail: { message: "connected " + this.bluetoothDevice.name },
        bubbles: true,
        composed: true
      });
      this.dispatchEvent(myEvent);
    }
  }
  _handleNotifyChanged(evt:any)
  {
    if (this.connectedStr == 'connected')
    {
     // let value = (event.target as any).value;

	   // value = value.buffer ? value : new DataView(value);
	
	    let myEvent = new CustomEvent('data-event', {
        detail: { message: evt.detail.message },
        bubbles: true,
        composed: true
      });
      this.dispatchEvent(myEvent);
    }
  }

}

