<script lang="ts">
  import {
    extent,
    scaleLinear,
    scaleTime,
    line,
    curveBasis,
    select as d3_select,
    selectAll as d3_selectAll,
    easeLinear as d3_easeLinear,
    zoom as d3_zoom,
    axisBottom as d3_axisBottom,
    axisTop as d3_axisTop,
    max as d3_max,
  } from "d3";
  import { onMount } from "svelte";
  import {  eTrainingStatus, GraphData,  eContractRelax, nrOfTicksPerTypes,  TICKS_PER_SEC, SignalElement} from "../utils/utils";

  // props
  export let data:GraphData;
  export let status = eTrainingStatus.IDLE;
  export let relaxSound;
  export let contractSound;
  export let duration: Number;
  export let repeat: Number;
  export let nrSecsOnScreen = 15; //depends also on screenwidth

  // derivetives
  var time = duration * repeat * 2;
  //$: curveData = data.D3signal;
  //var timeData = data.typeTimeline;
  

  // settings
  var totalSeconds = 0;


  // with xScale we 



  /*
	    the zoom factor is  total seconds / desired_sec_to_show  (for now 5)
		ViewBox is standard 100 by 100 , draw the graph inside  100/zoomfactor (width and hight) and then zoom in and start panning
	*/

  let minXval = 20;
  let maxXval = 80;
  let maxXdiff = maxXval - minXval;
  let middleY = 45;
  let maxYdiff = 35;

  // maybe Y needs to be zoom so calculate how much d3 scale is changing the x axis
  $: d3Zoom =
    d3_max(data.D3signal, function (d) {
      return +d.tick;
    }) / maxXdiff;
 

  // scales, fit all the data (domain) inside the range, max range is 0..100 if the viewbox is 100
  $: xScale = scaleLinear()
    .domain(extent(data.D3signal.map((d) => d.tick)))
    .range([minXval, maxXval]);

  $: yScale = scaleLinear()
    .domain(extent(data.D3signal.map((d) => d.ContractOrRelax)))
    .range([middleY-(maxYdiff/d3Zoom), middleY+(maxYdiff/d3Zoom) ]);

  // the path generator
  $: pathLine = line()
    .x((d) => xScale(d.tick))
    .y((d) => yScale(d.ContractOrRelax))
    .curve(curveBasis);

  var marker;
  var moveBox;
  var svgSelect;
  var paused = false;
  var pausedRemainingtime = 0;
  let oneSec = 1000; // 1000ms is normal speed , 100 is 10 times faster
  var animLoopCnt = 0;

  $: parentNode = null;

  $: {
    dataIsChanged(data); // $: means it is reactive in svelte, so when data change the function is called
  }

  $: {
    statusIsChanged(status);
  }

  $: radius = 3 / d3Zoom;
  $: strokeWidth = 3 / d3Zoom;

  var length =0;
 


  function dataIsChanged(newValue) {




    paused = false;

	length = 0;
	currentlength = 0;
	pausedRemainingtime = 0;
  animLoopCnt = 0;
  }

  async function animation(){


    //contractSound.play();
    //relaxSound.play(); 


      length = data.typeTimeline.length;
      console.log("lenght :", length);
      var positionY1;
      var positionY2;
      var positionX1;
      var positionX2;


      let minYreal = yScale(0)-1; // give some space to the circles
      let middleY = 45;
      let maxYdiff = 35;
      let diffFromMid = (middleY - minYreal)
      if (diffFromMid == 0) diffFromMid = 1;
      let zoomY = maxYdiff / diffFromMid;
      
      positionX1 = data.typeTimeline[0].tick;
      positionX2 = data.typeTimeline[1].tick;
      let viewboxSize = ((xScale(positionX2)-xScale(positionX1))*2.2);

      let shiftToright = 4;
      let Ycomp = 1;

      if (!paused)
      {
        svgSelect = await d3_select("svg")
               .transition()
               .duration(100)
               .ease(d3_easeLinear)
               .attr("viewBox", (xScale(positionX1)-shiftToright) + " " +  (minYreal) + " " + viewboxSize + " " + viewboxSize + Ycomp)
               .end();
        await svgSelect;

        // this 2e line is needed because the zoom in of the first line is not complete somehow..
        svgSelect = await d3_select("svg")
               .transition()
               .duration(100)
               .ease(d3_easeLinear)
               .attr("viewBox", (xScale(positionX1)-shiftToright) + " " +  (minYreal) + " " + viewboxSize + " " + viewboxSize + Ycomp)
               .end();
        await svgSelect;

      }
      paused = false;

      for (; (animLoopCnt < length) && (!paused); animLoopCnt=animLoopCnt+1)
      { 
        positionX1 = data.typeTimeline[animLoopCnt].tick;

        if (animLoopCnt+1 < length)
        {        positionX2 = data.typeTimeline[animLoopCnt+1].tick; } else { positionX2 = positionX1 + nrOfTicksPerTypes.relax; }
        var curve =[];
        console.log("type = ",data.typeTimeline[animLoopCnt].ContractOrRelax);

        if (data.typeTimeline[animLoopCnt].ContractOrRelax < 2)
        { 
          time =  data.typeTimeline[animLoopCnt].ContractOrRelax == eContractRelax.CONTRACT ?  nrOfTicksPerTypes.contract : nrOfTicksPerTypes.relax;
          positionY1 = data.typeTimeline[animLoopCnt].ContractOrRelax;
          positionY2 = data.typeTimeline[animLoopCnt].ContractOrRelax;
          curve = [ {tick: positionX1, ContractOrRelax: positionY1}, {tick: positionX2, ContractOrRelax: positionY2}];
        }else
        {  if (data.typeTimeline[animLoopCnt].ContractOrRelax != eContractRelax.START) {
          time =  nrOfTicksPerTypes.curve;
          positionY1 = data.typeTimeline[animLoopCnt].ContractOrRelax == eContractRelax.CURVE_UP ? eContractRelax.RELAX : eContractRelax.CONTRACT;
          positionY2 = data.typeTimeline[animLoopCnt].ContractOrRelax == eContractRelax.CURVE_UP ? eContractRelax.CONTRACT : eContractRelax.RELAX; 
          
          if (data.typeTimeline[animLoopCnt].ContractOrRelax == eContractRelax.CURVE_UP)
             contractSound.play();
          else
            relaxSound.play();

          let diff = data.typeTimeline[animLoopCnt+1].tick - data.typeTimeline[animLoopCnt].tick
          for (var x=0; x < nrOfTicksPerTypes.curve/2; x++)  
          { var ElementEntry:SignalElement = {tick:data.typeTimeline[animLoopCnt].tick +x ,ContractOrRelax: positionY1}
            curve.push(ElementEntry);
          }
          for (var x= nrOfTicksPerTypes.curve/2; x < nrOfTicksPerTypes.curve; x++)  
          { var ElementEntry:SignalElement = {tick:data.typeTimeline[animLoopCnt].tick +x , ContractOrRelax: positionY2}
            curve.push(ElementEntry);
          }
          } else { positionY2 = eContractRelax.RELAX; positionY1 = eContractRelax.RELAX; time = nrOfTicksPerTypes.start; curve = [ {tick: positionX1, ContractOrRelax: positionY1}, {tick: positionX2, ContractOrRelax: positionY2}]} 
        }
        time = time/TICKS_PER_SEC;
       
        
        console.log(" pox x y x y", positionX1, ":", positionY1, ":",  positionX2, ":", positionY2);
        parentNode =  document.getElementById("pathForMarker"); //d3_select("#pathForMarker");

       // parentNode.d ="";

        parentNode.setAttribute("d",  pathLine(curve));
        
        svgSelect = d3_select("svg")
               .transition()
               .duration(time * oneSec)
               .ease(d3_easeLinear)
               .attr("viewBox", (xScale(positionX2)-shiftToright) + " " + (minYreal) + " " + viewboxSize + " " + viewboxSize + Ycomp);

       
        await transitionMarker();
      //  console.log("path", parentNode.d)
        marker.transition().remove();  // if any transition remove
        
      }

    

  }


  function statusIsChanged(status) {
    if (status == eTrainingStatus.PLAY) {
  
      if (!paused) {
        animLoopCnt = 0; // start at the beginning

      }
      animation();
   
      
    } else {
      if (status == eTrainingStatus.PAUSED) {
          paused = true;
          marker.transition()   // stop de animaties
          d3_select("svg").transition();
      }
      
    }
  }

  async function transitionMarker() {
    
    await marker
      .transition()
      .duration(time * oneSec)
      .ease(d3_easeLinear)
      .attrTween("transform", translateAlong(parentNode))
      .end();

  }

  var lastval = 0; // the last y val of the circle that is moving
  var playing = 0; // 0 = no playing, 1 = relax, 2 = contract
  var speedStep = 0;
  var currentlength = 0;
  var correctionLenght = 0;

  
  function translateAlong(path) {
    var l = path.getTotalLength();
    console.log("length:",path.getTotalLength())
    return function (i) {
      return function (t) {
        // t goes from 0..1  in steps according to the duration, with d3_easeLinear this should be liniear in time?, but it's not
        var p = path.getPointAtLength(t*l);
        lastval = p;
       // console.log("t",t,"x",p.x,"y",p.y)
        return "translate(" + p.x + "," + p.y + ")"; //Move marker
      };
    };
  }

  /* TODO: set these 2 function in util.ts */
  function cssVariables(node, variables) {
    setCssVariables(node, variables);

    return {
      update(variables) {
        setCssVariables(node, variables);
      },
    };
  }

  function setCssVariables(node, variables) {
    for (const name in variables) {
      node.style.setProperty(`--${name}`, variables[name]);
    }
  }

  onMount(() => {
    moveBox = d3_select("#test");

    parentNode = d3_select("#pathForMarker");

    marker = d3_select("#circle");
    svgSelect = d3_select("svg");

    marker.attr("transform", "translate(" + xScale(data.typeTimeline[0].tick) + "," + yScale(eContractRelax.RELAX) + ") " );
   });



</script>

<svg
  id="test"
  class="borderRed"
  viewBox="0 0 100 100"
  style="height:100%; width:100%"
>
  <g id="moveBox">
    <path use:cssVariables={{ strokeWidth }} id="foo" d={pathLine(data.D3signal)} />
    <path use:cssVariables={{ strokeWidth }} id="pathForMarker" style="stroke:white"  />
    <g id = "circle">
      <circle id="circle1" r={radius} cx="0" cy="0" fill="white" class="circle" />
      <circle id="circle1" r={radius*4} cx="0" cy="0" fill="white" class="transparent" />
    </g>
  </g>
</svg>

<style>
  path {
    stroke: rgb(211, 120, 174);
    stroke-width: var(--strokeWidth);
    fill: none;
    stroke-linecap: round;
  }

  .transparent { 
    opacity: 0.5;
  }

  .borderRed {
     /*border: 1px solid red; */
  }

  .axis {
    font: 4px sans-serif;
  }

  svg {
    max-height: 100%;
    max-width: 100%;
  }

  .transformToMiddle {
    position: relative;
    left: 0%;
    transform: translate(0%, 20%);
  }
</style>
