import {
    Semaphore
  } from 'await-semaphore';

enum BluetoothGattState {
    Disconnected = 1,
    Connecting,
    Connected,
    Disconnecting,
    Error,
}

export default class BTLEclass extends EventTarget {
    bluetoothDevice: any = null;
    connectionStr: string = 'disconnected';
    gatServer: any = null;
    currentService:any = null;
    currentCharacteristics:any = null;
    notifyCharacteristics: any = null;
    primaryServiceObject = [] as any[];

    // process is not defined error https://github.com/notenoughneon/await-semaphore/issues/7
  //  semaphore = new Semaphore(1);
    userDisconnect:boolean = false;

    maxretry: number = 0;
    //   disconnectble: boolean = false; // on purpose disconnect
    //   connected: boolean = false;
    state: BluetoothGattState = BluetoothGattState.Disconnected;

    constructor(bluetoothDevice: any, primaryServiceObject: any[]) {
        super();
        this.bluetoothDevice = bluetoothDevice;
        this.state = BluetoothGattState.Disconnected;
        this.primaryServiceObject = primaryServiceObject;
        this.SetNotify = this.SetNotify.bind(this);
        this.handleNotifyChanged = this.handleNotifyChanged.bind(this);
    }

    private log(logStr: String) {
        console.log(logStr);
    }

    private setState(newState: BluetoothGattState) {
        let logstr: String = "old state = " + BluetoothGattState[this.state] + " new state = " + BluetoothGattState[newState];
        this.log(logstr);
        this.state = newState;
        switch (this.state) {
            case BluetoothGattState.Disconnected:
                this.currentService = null;
                this.currentCharacteristics = null;
                this.connectionStr = 'connect';
                break;

            case BluetoothGattState.Connecting:
                this.connectionStr = 'connecting'
                break;
            case BluetoothGattState.Disconnecting:
                this.connectionStr = 'disconnecting'
                break;

            case BluetoothGattState.Connected:
                 this.connectionStr = 'connected'
                break;

            default:
                this.connectionStr = 'error'
                break;

        }
        this.dispatchEvent(new Event('state-change'));
    }

    async timeout() {
        if (!this.userDisconnect)
        { 
            if ((this.maxretry < 10) && (this.state === BluetoothGattState.Connecting)) {
                this.maxretry++;
                this.connectit();
            }
        }
    }

    connect() // entry point for connect from outside
    {
        this.userDisconnect = false; 

        if (this.state === BluetoothGattState.Disconnected) // its disconnected so ready to connect
        {
            this.setState(BluetoothGattState.Connecting);
            this.maxretry = 0;
            this.connectit();
        }

    }

    disconnect(classInstance: BTLEclass) {

        // user disconnected so different then an internal disconnect (and reconnect retry)
        this.userDisconnect = true;

        if (!this.bluetoothDevice) {
            return;
        }

        this.setState(BluetoothGattState.Disconnecting);
        console.log('Disconnecting from Bluetooth Device...');
        if (this.bluetoothDevice.gatt.connected) {
            this.bluetoothDevice.gatt.disconnect();
        } else {
            console.log('> Bluetooth Device is already disconnected');
            this.setState(BluetoothGattState.Disconnected);
        }
        this.bluetoothDevice = null;

    }

    async  connectit() // called by connect and the timeout
    {
        try {

            if (this.state != BluetoothGattState.Connected)
            {
                this.gatServer = await this.bluetoothDevice.gatt.connect();
                this.setState(BluetoothGattState.Connected); // if not connected it will goto the catch 
            }

        } catch (error) {
            console.log("connecting failed  \n");
            this.gatServer = await this.bluetoothDevice.gatt.disconnect();
            if (this.state === BluetoothGattState.Connecting)
            {
                console.log("try again over 20 seconds \n");
                setTimeout(this.timeout.bind(this), 20000);
            }
        }
    }

    onDisconnected() {
        console.log('> Bluetooth Device disconnected');

        switch (this.state) {
            case BluetoothGattState.Disconnecting:
            case BluetoothGattState.Error:
                this.setState(BluetoothGattState.Disconnected);
                break;

            default:
                this.setState(BluetoothGattState.Disconnected);
                this.connect(); // connect again
                break;

        }

    }

    async getCharacteristic(serviceNr:number)
    {
       // if (this.currentService == null)
        {
             this.currentService = await this.gatServer.getPrimaryService(this.primaryServiceObject[serviceNr][0].service);
        }
       // if (this.currentCharacteristics == null)
             this.currentCharacteristics = await this.currentService.getCharacteristic(this.primaryServiceObject[serviceNr][0].characteristic);
    }

    async readvalueFromService(serviceNr:number) {
        if (this.state === BluetoothGattState.Connected) {

          //  let release = await this.semaphore.acquire();

            try {
                await this.getCharacteristic(serviceNr); // fills this.currentCharacteristic 
                let value = await this.currentCharacteristics.readValue();
                let batteryLevel = value.getUint8(0);
             //   release();
                return value;
            }
            catch (error) {
                console.log('Argh! ' + error);
             //   release();
            };

        }
    }

    async SetNotify(onOff:boolean, serviceNr:number)
    {
        if (this.state === BluetoothGattState.Connected) {
          //  let release = await this.semaphore.acquire();

            try {
                await this.getCharacteristic(serviceNr); // fills this.currentCharacteristic
                if (onOff === true) 
                {
                    await this.currentCharacteristics.startNotifications();
                    this.notifyCharacteristics = this.currentCharacteristics;
                    console.log("notify set")
                    this.notifyCharacteristics.addEventListener('characteristicvaluechanged',
                                  this.handleNotifyChanged);
                }
                else
                    await this.currentCharacteristics.stopNotifications();
            //    release();
            } catch (error) {
                console.log('Argh! ' + error);
            //    release();
            }
        }
 		
    }
    async  writevalueToService(value:DataView, serviceNr:number) {
        if (this.state === BluetoothGattState.Connected) {
            // process is not defined error https://github.com/notenoughneon/await-semaphore/issues/7
         //   let release = await this.semaphore.acquire();

            try {
                await this.getCharacteristic(serviceNr); // fills this.currentCharacteristic 
                await this.currentCharacteristics.writeValue(value);
           //     release();
            } catch (error) {
                console.log('Argh! ' + error);
           //     release();
            }
        }
    }
    
    handleNotifyChanged(evt: any) {

        let value = (event.target as any).value;

        value = value.buffer ? value : new DataView(value);

        let myEvent = new CustomEvent('notify-data-event', {
            detail: { message: value },
            bubbles: true,
            composed: true
        });
        this.dispatchEvent(myEvent);

    }

}

