
export  enum eContractRelax{
	CONTRACT = 0,
	RELAX = 1,
	CURVE_UP = 2,
	CURVE_DOWN = 3,
	START = 4
  }
 
  export interface NrOfTicksPerTypes{ 
    contract : number, 
    relax    : number, 
    curve    : number, 
    start    : number
}

let DURATION = 5;
export let TICKS_PER_SEC = 10;

export let nrOfTicksPerTypes:NrOfTicksPerTypes = {contract : DURATION*TICKS_PER_SEC, 
	                                          relax : 3*TICKS_PER_SEC,
											 curve: 1*TICKS_PER_SEC,
											 start: 4*TICKS_PER_SEC
										
										} 



export type SignalElement = {tick: number, ContractOrRelax: eContractRelax};   // time, value
export type GraphData = {D3signal:SignalElement[], typeTimeline:SignalElement[]}

var UtilsTick = 0;


export  enum eTrainingStatus{
	IDLE = 0,
	PLAY = 1, 
	PAUSED = 2,
	STOP = 3
  }

  
