export function timeFormat(nrSeconds: number): string {

		var sec_num = nrSeconds;
		var hours   = Math.floor(sec_num / 3600);
		var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
		var seconds = sec_num - (hours * 3600) - (minutes * 60);
	
		if (hours   < 10) {hours   = "0"+hours;}
		if (minutes < 10) {minutes = ""+minutes;}
		if (seconds < 10) {seconds = "0"+seconds;}
		return minutes+':'+seconds;
	
}

export function addTicks(action:eContractRelax, data:SignalElement[],nrTicks:number)
{
	if (action == eContractRelax.START)
	  action = eContractRelax.RELAX; // same signal/path as for relax
    for (var x:number = UtilsTick; x < (UtilsTick+nrTicks) ;x++){
        
        var ElementEntry:SignalElement = {tick:x , ContractOrRelax:action}
        data.push(ElementEntry)
    }
    UtilsTick += nrTicks;

    return data;
}

function createPart(action:eContractRelax, data:GraphData)
{
    /* add the action to the timeline */
	data.typeTimeline.push({tick:UtilsTick,ContractOrRelax:action});
	switch (action) {
		case eContractRelax.START:
            addTicks(action, data.D3signal, nrOfTicksPerTypes.start);
		break;
		case eContractRelax.CONTRACT:
			addTicks(action, data.D3signal, nrOfTicksPerTypes.contract);
		break;
		case eContractRelax.RELAX:
			addTicks(action, data.D3signal, nrOfTicksPerTypes.relax);
		break;
		case eContractRelax.CURVE_UP:
			addTicks(eContractRelax.RELAX, data.D3signal, nrOfTicksPerTypes.curve/2); //  half signal down
		    addTicks(eContractRelax.CONTRACT, data.D3signal, nrOfTicksPerTypes.curve/2); //  half signal down
		break;
		case eContractRelax.CURVE_DOWN:
			addTicks(eContractRelax.CONTRACT, data.D3signal, nrOfTicksPerTypes.curve/2); //  half signal down
		    addTicks(eContractRelax.RELAX, data.D3signal, nrOfTicksPerTypes.curve/2); //  half signal down
		break;
	}
}
// not used anymore, delete ?
export function createSignal(duration: number, nrOfRepeats: number){

    var data:GraphData = {D3signal:[], typeTimeline:[]};
    var time:Number = 0;
	var currentAction = eContractRelax.RELAX;


	UtilsTick = 0;

	createPart(eContractRelax.START, data);
   
    for (var x:number = 0; x < nrOfRepeats*2; x++){
        // flip contract/release
        if (currentAction == eContractRelax.RELAX) 
		{
			createPart(eContractRelax.CURVE_UP, data);
			createPart(eContractRelax.CONTRACT, data);
            currentAction = eContractRelax.CONTRACT;
		}
        else
		{
			createPart(eContractRelax.CURVE_DOWN, data);
			createPart(eContractRelax.RELAX, data);
            currentAction = eContractRelax.RELAX;
		}
    }
	
    return data;
}

